html {
  box-sizing: border-box;
}
*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: #4a2b91;
  height: 100vh;
  font-family: 'Roboto', sans-serif;
}
.App {
  color: #fff;
  text-align: center;
}

.container-fluid {
  padding: 0;
}

.logo {
  width: 200px;
  padding-left: 50px;
}

.btn {
  color: #fff;
  font-weight: 600;
  border-radius: 100px;
  background-color: #4a2b91;
  width: 170px;
}

.btn:hover {
  color: #fff;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.6);
}

.nav-item {
  padding-right: 30px;
}

.nav-link {
  color: #4a2b91 !important;
  font-weight: 700;
}

.nav-link:hover {
  text-decoration: underline;
}

.title-image {
  width: 20%;
  position: absolute;
  right: 300px;
}

.container {
  padding: 7% 15%;
  text-align: left;
}

.heading {
  font-size: 2rem;
  margin-bottom: 30px;
}

.heading-detail {
  font-size: .8rem;
}

.left-content {
  margin-top: 20px;
}

.step {
  font-size: 0.6rem;
  letter-spacing: 3px;
  font-weight: 700;
}

.self {
  color: #00e1ff;
  font-weight: 600;
  padding-left: 20px;
}

.step-function {
  margin-bottom: 10px;
}